.brands {
    background-color: #383838;
    .container {
        position: relative;
    }
        &__navs {
            @include flex-c-c;
        }
        .swiper-button-prev,
        .swiper-button-next {
            width: 30px;
            height: 30px;
            border-radius: 4px;
            background-color: $mainClr;
            z-index: 1;
            &:after {
                display: none;
            }
            svg {
                width: 20px;
                height: 25px;
                fill: $greydark;
            }
            @media(max-width: 1295px) {
                position: relative;
                left: 0!important;
                right: 0!important;
                margin: 20px 5px;
            }
        }
        .swiper-button-disabled {
            background-color: $greydark;
            opacity: 1;
            svg {
                fill: $mainClr;
            }
        }
        .swiper-button-prev {
            left: -40px;
        }
        .swiper-button-next {
            right: -40px;
        }
    .swiper-wrapper {
        height: 160px;
        @media (max-width: 660px) {
            height: 120px;
        }
    }
    &__item {
        @include flex-c-c;
        height: 100%;
        background-color: #363636;
        padding: 35px;
        transition: all .3s ease-in;
        @media (max-width: 970px) {
            padding: 25px;
        }
        @media (max-width: 660px) {
            padding: 20px 15px;
        }
        img {
            object-fit: contain;
        }
        &:hover {
            background-color: #333333;
        }
    }
}