.skills {
    background-color: #383838;
    text-align: center;
    padding-bottom: 55px;
    .section__title-text {
        color: $greydark;
    }
    &__list {
        display: flex;
        justify-content: center;
        margin: 50px -12.5px 0;
        flex-wrap: wrap;
        @media (max-width: 880px) {
           margin: 0 -5px;
        }
        @media (max-width: 320px) {
           margin: 0;
        }
    }
}
.skill {
    &__block {
      display: flex;
      flex-direction: column;
        width: calc(100% / 6);
        padding: 0 25px;
          @media (max-width: 800px) {
            padding: 0 10px;
        }
        @media (max-width: 690px) {
          width: calc(100% / 4);
      }
        @media (max-width: 320px) {
            width: calc(100% / 3);
            padding: 0 0 15px 0;
        }
        p {
          font-size: 14px;
          line-height: 22px;
          color: #8a8a8a;
          font-weight: 800;
          text-align: center;
          text-transform: uppercase;
          @media (max-width: 768px) {
            font-size: 13px;
        }
        }
    }
   
    
}
.works {
    text-align: center;
    overflow: hidden;
    .section__title {
        color: #383838;
    }
    .btn {
        display: flex;
    }
    &__list {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin-bottom: 50px;
    }
    &__block {
        display: flex;
        align-items: stretch;
        align-content: stretch;
        padding: 0 1px;
        margin-bottom: 1px;
        width: calc(100% / 6);
        height: 250px;
        border-radius: 4px;
        transition: all .5s ease-in;
        &:hover {
            .item-work__content {
                opacity: 1;
                transform: scale(1) rotate(0);
            }
        }
        @media(max-width: 1440px) {
            width: calc(100% / 5);
        }
        @media(max-width: 1190px) {
            width: calc(100% / 4);
        }
        @media(max-width: 960px) {
            width: calc(100% / 3);
        }
        @media(max-width: 730px) {
            width: calc(100% / 2);
        }
        @media(max-width: 470px) {
            height: 200px;
        }
        @media(max-width: 320px) {
            width: 100%;
            height: 200px;
        }
    }
}
.item-work { 
    position: relative;
    width: 100%;
    height: 100%;
    align-items: stretch;
    align-content: stretch;
    border-radius: 4px;
    &__img {
        width: 100%;
        height: 100%;
        @include flex-c-c;
        img {
            border-radius: 4px;
        }
    }
    &__content {
        @include pos;
        width: 100%;
        height: 100%;
        padding: 50px 30px;
        background-color: #1ca399;
        opacity: 0;
        transform: scale(0) rotate(-45deg);
        transition: all .8s ease-in;
        border-radius: 4px;
    }
    &__title {
        font-size: 16px;
        color: #e4e4e4;
        font-weight: 800;
        line-height: 100%;
        margin-bottom: 10px;
        text-transform: capitalize;
    }
    &__subtitle {
        font-size: 12px;
        line-height: 22px;
        color: #e4e4e4;
        font-weight: 500;
        margin-bottom: 10px;
        text-transform: capitalize;
    }
    &__links {
        margin-bottom: 40px;
        a {
            @include flex-i-c;
            width: 39px;
            height: 39px;
            border-radius: 3px;
            background-color: #e4e4e4;
            transform: rotate(45deg);
            svg {
                width: 15px;
                height: 15px;
                fill: $mainClr;
                transform: rotate(-45deg);
                transition: all .5s ease;
            }
            &:hover {
                background-color:#383838;
                svg {
                    fill: $greydark;
                }
            }
        }
    }
    &__heart {
        svg {
            width: 12px;
            height: 12px;
            margin-right: 10px;
            fill: $greydark;
        }
        span {
            font-size: 12px;
            line-height: 22px;
            color: #e4e4e4;
        }
    }
}






  .circle {
    width: 120px;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    @media (max-width: 1010px) {
      width: 100px;
      height: 100px;
    }
    @media (max-width: 820px) {
      width: 80px;
      height: 80px;
    }
    @media (max-width: 425px) {
      width: 60px;
      height: 60px;
    }
  }
  
  .circle canvas {
    vertical-align: top;
    width: 120px!important;
    height: 120px!important;
    @media (max-width: 1010px) {
      width: 100px!important;
    height: 100px!important;
    }
    @media (max-width: 820px) {
      width: 80px!important;
    height: 80px!important;
    }
    @media (max-width: 425px) {
      width: 60px!important;
      height: 60px!important;
    }
  }
  
  .circle strong {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    font-size: 30px;
    color: $greydark;
    font-weight: 800;
    @media (max-width: 1010px) {
      font-size: 24px;
    }
    @media (max-width: 820px) {
      font-size: 20px;
    }
    @media (max-width: 820px) {
      font-size: 16px;
    }
  }
  
  .circle strong i {
    font-style: normal;
    font-size: 14px;
    font-weight: 500;
  }
  
  
  