body,
html {
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    color: #7a7a7a;
}
*{
    box-sizing: border-box;
}
*:focus{
    outline: none;
}
a {
    text-decoration: none;
    cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6 + p {
    margin-top: 0;
}
p {
    margin: 15px 0;
}
img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
ul {
    margin: 0;
    padding: 0;
    li {
        list-style: none;
    }
}
button {
    border: none;
}
button,
input,
select,
textarea {
    font-family: inherit;
}
$mainClr: #00a99d;
$greydark: #e4e4e4;
$dark-grey: #383838;

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}
.container-fluid {
    max-width: 100%;
    padding: 0;
}
h6 {
    font-size: 0.875rem;
    line-height: 100%;
    text-transform: uppercase;
    color: #606060;
    font-weight: 800;
    margin-top: 0;
    margin-bottom: 20px;
    @media (max-width: 500px) {
        font-weight: 700;
    }
}
section {
    position: relative;
    padding: 75px 0;
    @media (max-width: 768px) {
        padding: 50px 0;
    }
    .section__icon {
        position: absolute;
        top: -26px;
        left: 50%;
        @include flex-i-c;
        width: 53px;
        height: 53px;
        border-radius: 3px;
        background-color:  $mainClr;
        transform: translateX(-50%) rotate(45deg);
        transition: all .3s ease-in;
        z-index: 1;
        svg {
            width: 23px;
            height: 23px;
            fill: $greydark;
            transform: rotate(-45deg);
        }
        &:hover {
            background-color: $greydark;
            svg {
                fill: $mainClr;
            }
        }
        @media (max-width: 768px) {
            width: 45px;
            height: 45px;
            top: -21.5px;
            svg {
                width: 18px;
                height: 18px;
            }
        }
        @media (max-width: 425px) {
            width: 35px;
            height: 35px;
            top: -17.5px;
            svg {
                width: 15px;
                height: 15px;
            }
        }
    }
}
.btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    line-height: 29px;
    border-radius: 4px;
    background-color: #dedede;
    font-size: 10px;
    color: #8a8a8a;
    font-weight: 800;
    text-transform: uppercase;
    padding: 0 10px;
    transition: all .3s ease-in;
    &:hover {
        background-color: $mainClr;
        color: #e3e3e3;
    }
}
.btn-blue {
    background-color: $mainClr;
    color: $greydark;
    transition: all .3s ease-in;
    &:hover {
        background-color: $dark-grey;
    }
}
.btn-lg {
    line-height: 40px;
    font-size: 13px;
    padding: 0 15px;
    @media (max-width: 520px) {
        line-height: 29px;
        font-size: 10px;
        padding: 0 10px;
    }
}
.section__title{
    margin-bottom: 20px;
    @media (max-width: 768px) {
        margin-bottom: 13px;
    }
    &-text{
        position: relative;
        @include adaptFont(20, 26);
        line-height: 100%;
        color: #383838;
        font-weight: 800;
        text-align: center;
        text-transform: uppercase;
        display: inline-block;
        padding: 0 50px;
        @media (max-width: 768px) {
            padding: 0px 40px;
            svg {
                width: 22px!important;
                height: 12px!important;
            }
        }
        .color {
            color: $mainClr;
        }
        svg {
            position: absolute;
            content: '';
            top: 50%;
            transform: translateY(-50%);
            width: 28px;
            height: 15px;
            fill: $mainClr;
        }
        .left {
            left: 0;
        }
        .right {
            right: 0;
        }
    }
}
.section__subtitle {
    font-size: 16px;
    line-height: 120%;
    color: #606060;
    font-weight: 500;
    margin: 0 0 50px 0;
    @media (max-width: 620px) {
        font-size: 14px;
    }
}
.section__title-color {
    color: $greydark;
}

.section__title-line {
    position: relative;
    overflow: hidden;
    text-align: center;
    &:after,
    &:before {
        border-top: 1px solid #dedede;
        border-bottom: 1px solid #dedede;
        content: "";
        display: inline-block;
        height: 6px;
        position: relative;
        vertical-align: text-top;
        width: 50%;
        @media (max-width: 768px) {
            display: none;
        }
    }
    &:before {
        right: 10px;
        margin-left: -50%;
    }
    &:after {
        left: 10px;
        margin-right: -50%;
    }
}
