.teams {
    text-align: center;
    &__inner {
        position: relative;
        .swiper-button-prev,
        .swiper-button-next {
            top: -96px;
            width: 30px;
            height: 30px;
            border-radius: 4px;
            background-color: $mainClr;
            z-index: 1;
            &:after {
                display: none;
            }
            svg {
                width: 20px;
                height: 25px;
                fill: $greydark;
            }
        }
        @media (max-width: 890px) {
            padding-bottom: 70px;
            .swiper-button-next {
                top: 100%;
                right: 45%!important;
            }
            .swiper-button-prev {
                top: 100%;
                left: 45%!important;
            }
        }
        .swiper-button-disabled {
            background-color: $dark-grey;
            opacity: 1;
        }
        .swiper-button-next {
            right: 0;
        }
        .swiper-button-prev {
            left: 0;
        }
    }
}

.team {
    &__block {
        display: block;
        &:hover .team__bottom-body > .btn {
            background-color: #383838;
        }
    }
    &__top-body {
        position: relative;
        width: 100%;
        height: 270px;
        overflow: hidden;
        &:hover {
            .team__social-list {
                opacity: .7;
                transform: scale(1);
            }
        }
    }
    &__img {
        display: block;
        border-radius: 4px;
    }
    &__social-list {
        @include pos;
        width: 100%;
        height: 100%;
        background-color: $mainClr;
        @include flex-c-c;
        opacity: 0;
        transform: scale(0);
        transition: all .3s ease-in;
    }
    &__social-link {
        @include flex-i-c;
        width: 39px;
        height: 39px;
        border-radius: 3px;
        background-color: #ffffff;
        transition: all .3s ease-in;
        opacity: 1;
        transform: rotate(45deg);
        svg {
            transform: rotate(-45deg);
            width: 15px;
            height: 15px;
            fill: $mainClr;
        }
        &:hover {
            background-color: #383838;
            svg {
                fill: $greydark;
            }
        }
    }
    &__bottom-body {
        border-bottom: 1px solid #dedede;
        border-left: 1px solid #dedede;
        border-right: 1px solid #dedede;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        padding: 0  30px;
        margin-top: -20px;
        position: relative;
        z-index: 1;
        .btn {
            margin-bottom: 20px;
        }
      }
      &__position {
        display: block;
        font-size: 12px;
        line-height: 100%;
        color: #606060;
        padding-bottom: 15px;
        margin-bottom: 15px;
        border-bottom: 1px solid #ebebeb;
        text-transform: capitalize;

      }
      &__email,
      &__phone {
            position: relative;
            display: inline-block;
            color: #aaaaaa;
            padding-left: 30px;
            margin-bottom: 15px;
            transition: all .3s ease-in;
          svg {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            width: 15px;
            height: 15px;
            fill: #aaaaaa;
          }
          &:hover {
              color: $mainClr;
          }
      }

}

.quotes {
    background-color: $mainClr;
    text-align: center;
    &__slider {
        padding-bottom: 30px;
        .swiper-button-prev,
        .swiper-button-next {
            position: relative;
            display: inline-flex;
            width: 30px;
            height: 30px;
            border-radius: 4px;
            background-color: $dark-grey;
            left: 0;
            margin: 0 auto;
            &:after {
                display: none;
            }
            svg {
                width: 20px;
                height: 25px;
                fill: $greydark;
            }
        }
        .swiper-button-disabled {
            background-color: $greydark;
            opacity: 1;
            svg {
                fill: $dark-grey;
            }
        }
    }
    .section__icon {
        background-color: #383838;
        svg {
            transform: rotate(135deg)
        }
    }
    &__link {
        @include flex-i-c;
        height: 40px;
        border-radius: 4px;
        background-color: #e4e4e4;
        padding: 0 15px;
        margin-bottom: 25px;
        .name {
            font-size: 13px;
            color: #00a99d;
            font-weight: 800;
            text-transform: uppercase;
        }
        .position {
            font-size: 13px;
            color: #606060;
        }
    }
    P {
        @include adaptFont(14, 20);
        line-height: 140%;
        color: #e4e4e4;
        margin-bottom: 25px;
        q {
            quotes: "''" "''";
            position: relative;
            &::before,
            &::after  {
                font-size: 40px;
            }
        }
    }
}