.offer {
    text-align: center;
    .section__subtitle {
        color: #8a8a8a;
    }
    &__inner {
        @include flex-b-c;
        align-items: stretch;
        margin: 0 -12.5px;
        text-align: center;
        flex-wrap: wrap;
    }
    &__block {
        display: flex;
        padding: 0 25px;
        width: calc(100% / 3);
        @media (max-width: 685px) {
            width: 100%;
        }
    }
    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        flex-flow: column nowrap;
        @media (max-width: 685px) {
            text-align: left;
            padding-left: 80px;
            margin-bottom: 30px;
        }
        @media (max-width: 320px) {
            padding-left: 70px;
        }
    }
    &__icon {
        width: 100px;
        height: 100px;
        border-radius: 100%;
        background-color: #383838;
        margin: 0 auto 30px;
        @include flex-i-c;
        transition: all .3s ease-in;
        svg {
            width: 38px;
            height: 38px;
            fill: $greydark;
        }
        &:hover {
            background-color: $mainClr;
        }
        @media (max-width: 685px) {
            position: absolute;
            top: 0;
            left: 0;
            width: 80px;
            height: 80px;
            svg {
                width: 33px;
                height: 33px;
            }
        }
        @media (max-width: 768px) {
            width: 60px;
            height: 60px;
            svg {
                width: 25px;
                height: 25px;
            }
        }
        @media (max-width: 320px) {
            width: 45px;
            height: 45px;
            svg {
                width: 20px;
                height: 20px;
            }
        }
    }
    &__link {
        margin-top: auto;
    }
}
.features {
    background-color: #383838;
    text-align: center;
    padding-bottom: 30px;
    .section__title-text {
        color: $greydark;
    }
    &__inner {
        display: flex;
        text-align: left;
    }
    &__list {
        max-width: 360px;
        margin-top: 50px;
    }
    &__item {
        position: relative;
        margin-bottom: 45px;
        svg {
            @include pos;
            width: 40px;
            height: 40px;
            fill: $mainClr;
        }
        h6 {
            padding-left: 85px;
            color: $greydark;
        }
        p {
            padding-left: 85px;
        }
    }
}
.features__bottom {
    &-inner {
        @include flex-b-c;
        flex-wrap: wrap;
        margin: 0 -10px;
    }
    &-block {
        width: 33.333%;
        padding: 0 20px;
        margin-bottom: 30px;
        @media (max-width: 860px) {
            width: 50%;
        }
        @media (max-width: 520px) {
            width: 100%;
        }
    }
    &-item {
        display: flex;
        h6 {
            margin-bottom: 8px;
        }
    }
    &-img {
        flex: 0 0 40px;
        margin-right: 20px;
        width: 40px;
        display: flex;
        flex-direction: column;
        transition: all .3s ease-in;
        span {
            position: relative;
            display: block;
            width: 40px;
            height: 15px;
            background-color: $mainClr;
            z-index: -1;
            &:after {
                @include pos;
                top: 100%;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 15px 20px 0 20px;
                border-color: $mainClr transparent transparent transparent;
            }
        }
    }
    &-icon {
        position: relative;
        width: 40px;
        height: 35px;
        background-color: #383838;
        @include flex-i-c;
        &:after {
            @include pos;
            top: 100%;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 15px 20px 0 20px;
            border-color: #383838 transparent transparent transparent;
        }
        svg {
            width: 20px;
            height: 20px;
            fill: $greydark;
        }
    }
    &-subtitle {
        font-size: 12px;
        line-height: 100%;
        color: #aaaaaa;
        font-weight: 500;
    }
}