.prices {
    text-align: center;
    &__list {
        @include flex-b;
        flex-wrap: wrap;
        margin: 0 -7.5px;
        @media (max-width: 425px) {
            margin: 0 -4px
         }
    }
}
.price {
    &__block {
        display: flex;
        padding: 0px 15px;
        width: calc(100% / 4);
        transition: all .3s ease-in;
        @media(max-width: 880px) {
            width: calc(100% / 2);
            margin-bottom: 60px;
        }
        @media(max-width: 425px) {
            padding: 8px;
        }
        &:hover {
            .price__type {
                background-color: $mainClr;
            }
        }
    }
    &__item {
        display: flex;
        flex-direction: column;
        flex-flow: column nowrap;
        width: 100%;
        border: 1px solid #dedede;
        border-radius: 4px;        
        text-align: center;
        padding: 0 20px 20px 20px;
        @media (max-width: 425px) {
           padding: 0 10px 10px 10px;
        }
    }
    &__type {
        height: 40px;
        border-radius: 4px;
        background-color: $dark-grey;
        padding: 0 15px;
        font-size: 13px;
        color:  $greydark;
        text-transform: uppercase;
        white-space: nowrap;
        font-weight: 800;
        @include flex-i-c;
        width: 80%;
        margin: -20px auto 45px auto;
        position: relative;
        z-index: 1;
        transition: all .3s ease-in;
        @media (max-width: 400px) {
           font-size: 11px;
           height: 32px;
           padding: 0px 8px;
           font-weight: 700;
           margin-top: -16px;
           margin-bottom: 10px;
        }
    }
    &__price {
        position: relative;
        @include adaptFont(25, 60);
        color: #383838;
        font-weight: 800;
        margin-bottom: 25px;
        padding-bottom: 25px;
        border-bottom: 1px solid #ebebeb;
        @media (max-width: 400px) {
           margin-bottom: 15px;
           padding-bottom: 15px;
        }
        small {
            @include adaptFont(12, 16);
            vertical-align: text-top;
            display: inline-block;
            margin-left: -13px;
            @media(max-width: 660px) {
                margin-left: 0;
            }
        }
        .month {
            font-size: 12px;
            color: #8a8a8a;
            font-weight: 500;
        }
        .dollar {
            font-size: 16px;
            color: #8a8a8a;
            font-weight: 500;
        }
        .point {
            display: inline-block;
            width: 12px;
            height: 12px;
            border-radius: 4px;
            background-color: #383838;
            transform: rotate(45deg);
            margin-left: 7px;
            @media(max-width: 1020px) {
                width: 8px;
                height: 8px;
                margin-left: 5px;
                border-radius: 2px;
            }
            @media(max-width: 660px) {
               display: none;
            }
        }
    }
    &__btn {
        margin-top: auto;
        padding-top: 25px;
        border-top: 1px solid #ebebeb;
    }
    &__details {
        li {
            color: #8a8a8a;
            margin-bottom: 20px;
           @media (max-width: 768px) {
           margin-bottom: 15px;
           }
           @media (max-width: 425px) {
              margin-bottom: 10px;
              font-size: 12px;
           }
        }
    }

}