.footer {
    &__top-inner {
        background-color: $dark-grey;
        text-align: center;
        padding: 40px 0;
    }
    &__logo {
        width: 160px;
        height: 50px;
        margin: 0 auto 65px auto;
        img {
            object-fit: contain;
        }
        @media (max-width: 920px) {
            margin-bottom: 45px;
        }
        @media (max-width: 768px) {
            margin-bottom: 35px;
        }
        @media (max-width: 425px) {
           margin-bottom: 25px;
        }
    }
    &__sociaL-list {
        @include flex-c-c;
        margin-bottom: 35px;
        flex-wrap: wrap;
    }
    &__social-link{
        margin: 5px;
        @include flex-i-c;
        width: 44px;
        height: 40px;
        border-radius: 5px;
        border: 1px solid #606060;
        transition: all .3s ease-in;
        svg {
            width: 18px;
            height: 18px;
            fill: $greydark;
        }
        &:hover {
            background-color: $mainClr;
            border-color: $mainClr;
        }
        @media (max-width: 620px) {
            width: 38px;
            height: 36px;
            svg {
                width: 16px;
                height: 16px;
            }
         }
    }
}
// Footer map 
.footer {
    #map {
        width: 100%;
        height: 100%;
        
    }
    &__map {
        height: 500px;
        position: relative;
        overflow: hidden;
    }
    &__form-wrap {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
        margin: 0 auto;
        max-width: 1200px;
    }
    &__form {
        max-width: 368px;
        border-radius: 4px;
        background-color: #f9f9f9;
        padding: 20px 25px;
    }
    &__input {
        line-height: 40px;
    }
    &__input,
    &__textarea {
        border-radius: 4px;
        border: 1px solid #dedede;
        width: 100%;
        display: block;
        margin-bottom: 20px;
        font-size: 12px;
        color: #8a8a8a;
        font-weight: 500;
        padding: 0 20px;
        text-transform: capitalize;
        &:focus {
            border-color: $mainClr;
        }
    }
    &__textarea {
        padding-top: 10px;
        min-height: 220px;
        overflow-x: none;
    }
    &__bottom {
        text-align: center;
        padding: 50px 0 25px 0;
        background-color: $dark-grey;
        position: relative;
        @media (max-width: 425px) {
            padding: 35px 0 18px;
         }
    }
    &__copyright {
        font-size: 14px;
        color: #cccccc;
        font-weight: 800;
    }
    &__icon {
        position: absolute;
        top: -26.5px;
        left: 50%;
        transform: translateX(-50%);
        width: 53px;
        height: 53px;
        border-radius: 3px;
        background-color: #00a99d;
        transform: rotate(45deg);
        z-index: 1;
        @include flex-i-c;
        svg {
            width: 25px;
            height: 25px;
            transform: rotate(-45deg);
            fill: $greydark;
        }
        @media (max-width: 768px) {
            width: 45px;
            height: 45px;
            top: -21.5px;
            svg {
                width: 18px;
                height: 18px;
            }
        }
        @media (max-width: 425px) {
            width: 35px;
            height: 35px;
            top: -17.5px;
            svg {
                width: 15px;
                height: 15px;
            }
        }
    }


}