.facts {
    background-color: #383838;
    text-align: center;
    .section__title-text {
        color: $greydark;
    }
    &__list {
        @include flex-b-c;
        align-items: stretch;
        flex-wrap: wrap;
        margin: 0 -15px;
        @media (max-width: 500px) {
           margin: 0 -7.5px;
        }
    }
    &__block {
        padding: 0 15px;
        width: calc(100% / 4);
        overflow: hidden;
        @media (max-width: 860px) {
            width: calc(100% / 2);
            padding: 0 30px 20px 30px;
        }
        @media (max-width: 500px) {
            padding: 0 15px 30px 15px;
        }
    }
    &__item {
        overflow: hidden;
        h6 {
            font-size: 16px;
            span {
                font-size: 10px;
                font-weight: 500;
            }
            @media (max-width: 500px) {
                font-size: 14px;
            }
        }
    }
    &__number {
        position: relative;
        display: inline-block;
        @include adaptFont (30, 80);
        line-height: 100%;
        color: #e4e4e4;
        font-weight: 500;
        margin-bottom: 20px;
        transition: all .3s ease-in;
        &:after,
        &:before {
            position: absolute;
            content: '';
            top: 50%;
            transform: translateX(-50%) rotate(45deg);
            width: 12px;
            height: 12px;
            border-radius: 3px;
            background-color: #484848;
            @media (max-width: 500px) {
                width: 8px;
                height: 8px;
            }
        }
        &:after {
            left: -30px;
        }
        &:before {
            right: -30px;
        }
        &:hover {
            &:after,
            &:before {
                background-color: $mainClr;
            }
        }
    }
    &__icon {
        margin-bottom: 20px;
        position: relative;
        @include flex-c-c;
        svg {
            width: 20px;
            height: 20px;
            fill: $mainClr;
        }
        .icon-like {
            transform: rotate(180deg);
        }
        &:after,
        &:before {
            position: absolute;
            content: '';
            top: 50%;
            transform: translateX(-50%);
            width: 100%;
            height: 1px;
            background-color: #606060;
            margin: 0 20px;
        }
        &:after {
            left: 100%;
        }
        &:before {
            right: 0;
        }
    }
}

.purchase {
    background-color: $mainClr;
    padding: 20px 0;
    &__inner {
        @include flex-b-c;
        .btn {
            margin-left: 10px;
            color: #606060;
        }
        @media (max-width: 990px) {
            flex-wrap: wrap;
            justify-content: center;
            text-align: center;
            .btn {
                margin: 8px;
            }
        }
    }
    &__text {
        font-size: 16px;
        color: #e4e4e4;
        font-weight: 800;
        text-transform: uppercase;
    }

}